export function normalize(string) {
     string = string
          .toLowerCase()
          .normalize("NFD")
          .replace(/[\u0300-\u036f]/g, "");

     // les voyelles sont remplacées par une ligature
     string = string
          .replace(/œ/g, "oe")
          .replace(/æ/g, "ae")
          .replace(/[']/g, " ");

     return string;
}
